.post {
  padding: 40px 0 160px;
  // .post__title
  &__title {
    margin-bottom: 40px;
  }

  // .post__row
  &__row {
    align-items: flex-start;
    width: 100%;
  }

  // .post__content
  &__content {
    flex: 1 1 700px;
    margin-right: 70px;
  }

  // .post__bread-crumbs
  &__bread-crumbs {
    display: inline-block;
    margin-bottom: 45px;
    a {
      color: $text-color;
      font-weight: 400;
      text-decoration: none;
      svg {
        margin-right: 14px;

        transition: margin 0.3s ease-in-out;

        fill: $text-color;
      }
      &:hover {
        svg {
          margin-right: 7px;
        }
      }
    }
  }

  // .post__article
  &__article {
  }

  // .post__sidebar
  &__sidebar {
    flex: 1 1 400px;
  }
}

.post-article {
  // .post-article__image
  &__image {
    margin-bottom: 40px;
    picture {
      width: 100%;
    }
    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }

  // .post-article__date
  &__date {
    margin-bottom: 16px;
    .post-date {
      &__day {
        font-weight: 400;
        font-size: 40px;
      }
      &__month,
      &__line {
        font-weight: 400;
        font-size: 20px;
      }
    }
  }

  // .post-article__title
  br {
    line-height: 24px;
  }
  &__title {
    margin-bottom: 24px;

    font-weight: 400;
  }

  // .post-article__description
  &__description {
    font-weight: 350;
  }

  // .post-article__subtitle
  &__subtitle {
    margin-bottom: 24px;

    font-weight: 400;
  }

  // .post-article__list
  &__list {
    margin: 12px 0;
    padding-left: 20px;
    // .post-article__list-item
    &-item {
      margin-bottom: 12px;
      padding-left: 15px;

      font-weight: 350;

      list-style-image: url(../images/svg/icons/ic.svg);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.post-sidebar {
  // .post-sidebar__title
  &__title {
    margin-bottom: 32px;

    font-weight: 350;
  }

  // .post-sidebar__list
  &__list {
    flex-direction: column;
  }

  // .post-sidebar__item
  &__item {
    width: 100%;
    margin-bottom: 24px;
    padding: 32px 0 58px 32px;

    border-left: 1px solid #F5F5F5;
    &:last-child {
      margin-bottom: 0;
    }
    // .post-sidebar__item-row
    &-row {
      position: relative;

      justify-content: space-between;
      margin-bottom: 18px;
      &::after {
        position: absolute;
        top: 0;
        left: 0;

        width: 1px;
        height: 22px;

        background: $primary-color;
        transform: translate(-33px, 0);

        content: '';
      }
    }

    // .post-sidebar__item-date
    &-date {
      margin-right: 5px;
      .post-date {
        &__day {
          font-weight: 400;
          font-size: 20px;
        }
        &__month,
        &__line {
          font-weight: 400;
        }
      }
    }

    // .post-sidebar__item-link
    &-link {
      display: flex;

      text-decoration: none;
    }

    // .post-sidebar__item-description
    &-description {
      font-weight: 350;
    }
  }
}

// <1280
@include media-laptop {
  .post {
    padding: 40px 0 80px;
    &__title {
      margin-bottom: 30px;
    }
    &__bread-crumbs {
      margin-bottom: 20px;
    }
  }
}
// <992
@include media-tablet-horizontal {
}
// <768
@include media-tablet {
  .post {
    padding: 30px 0 60px;
    &__row {
      flex-direction: column;
    }
    &__content {
      margin: 0 0 20px;
    }
  }
  .post-sidebar {
    &__item {
      padding: 20px 0 30px 20px;
      &-row {
        &::after {
          transform: translate(-21px, 0);
        }
      }
    }
  }
  .post-article {
    &__image {
      margin-bottom: 30px;
    }
  }
}
// <480
@include media-mobile {
  .post {
    padding: 30px 0;
  }
  .post-article {
    &__image {
      margin-bottom: 20px;
    }
  }
}
// <320
@include media-mobile-small {
}
